$primary_color: #007cba;
$secondary_color: #5f5f5f;
$background_color: #dcdcde;
$white_color: #ffffff;

.fb-setting-responsive-control-wrapper {
    .fb-setting-responsive-control {
        margin-bottom: 0;
    }
}

.fb-setting-color-panel-btn {
    display: flex !important;
    width: 100%;
    .fb-setting-color-panel-btn-switchr {
        width: 100%;
        justify-content: center;
        height: unset;
        padding: 10px;
        line-height: 16px;
        min-height: 30px;
        font-size: 13px;
        font-weight: 400;
        color: $secondary_color !important;
        box-shadow: inset 0 0 0 1px $background_color;
        &.active {
            background-color: $background_color;
        }
    }
}

/** End General scss for Advanced Button **/

.dnd-items-wrapper {
    list-style: none;
    padding: 0;
    margin: 0;

    li.sortable-item {
        margin-bottom: 10px;
        margin-top: 10px;

        .sortable-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background: #f9f9f9;
            border: 1px solid #ddd;
            border-radius: 4px;

            .sortable-title {
                flex: 12;
                cursor: pointer;
                overflow: hidden;
                padding-left: 5px;
                text-overflow: ellipsis;
                white-space: nowrap;
                width: 200px;
            }

            .sortable-sort {
                border-right: 1px solid #b4b4cb;
                border-left: 1px solid #b4b4cb;
                button {
                    &:focus {
                        box-shadow: none;
                        outline: none;
                    }
                }
            }

            .sortable-delete {
                button {
                    color: #d63638;
                    &:focus {
                        box-shadow: none;
                        outline: none;
                    }
                }
            }
        }

        .sortable-item-content {
            padding-top: 10px;
        }
    }
}

button {
    &.components-button {
        &.fb-social-icon-single-btn {
            align-items: center;
            display: flex;
            flex: 1;
            justify-content: center;
            padding: 6px;
            height: unset;
            line-height: 16px;
            box-shadow: inset 0 0 0 1px #dcdcde;
            color: #5f5f5f;
            margin-bottom: 10px;
            &.active {
                background-color: #dcdcde !important;
                color: #5f5f5f !important;
            }
        }
        &:focus {
            outline: none !important;
            box-shadow: none !important;
        }
    }
}

.fb-animated-heading-wrapper {
    margin: 0;
}

// editor.scss
.fb-animated-heading-wrapper h1,
.fb-animated-heading-wrapper h2,
.fb-animated-heading-wrapper h3,
.fb-animated-heading-wrapper h4,
.fb-animated-heading-wrapper h5,
.fb-animated-heading-wrapper h6,
.fb-animated-heading-wrapper p {
    display: inline-block;
    margin: 0;
}

.fb-animated-heading-cursor {
    font-size: 40px;
    line-height: 40px;
    display: inline-block;
    vertical-align: middle;
}

.fb-animated-heading-swirl-wrapper {
    display: inline-block;
    transition: all .5s ease-in-out;
    vertical-align: bottom;
}
.fb-animated-heading-dynamic-wrapper {
  display: inline-block;
  position: relative;
  perspective: 300px;
  overflow: hidden;
  vertical-align: bottom;
}

.fb-animated-heading-dynamic-text {
  transform: scaleX(1.0005);
  white-space: nowrap;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.fb-animated-heading-dynamic-text.fb-animated-heading-text-active {
  position: relative;
  opacity: 1;
}

.fb-animated-heading-dynamic-letter {
  backface-visibility: hidden;
  display: inline-block;
  transform-style: preserve-3d;
  opacity: 0;
  transform: translateZ(-20px) rotateX(90deg);
  transition: transform 0.5s ease, opacity 0.5s ease;
}

.fb-animated-heading-dynamic-letter.fb-animated-heading-animation-in {
  animation: fb-animated-heading-swirl-in 0.4s forwards;
}

.fb-animated-heading-dynamic-letter.fb-animated-heading-animation-out {
  animation: fb-animated-heading-swirl-out 0.4s forwards;
}

@keyframes fb-animated-heading-swirl-in {
  0% { opacity: 0; transform: translateZ(-20px) rotateX(90deg); }
  60% { opacity: 1; transform: translateZ(-20px) rotateX(-10deg); }
  100% { opacity: 1; transform: translateZ(-20px) rotateX(0deg); }
}

@keyframes fb-animated-heading-swirl-out {
  0% { opacity: 1; transform: translateZ(-20px) rotateX(0); }
  60% { opacity: 0; transform: translateZ(-20px) rotateX(-100deg); }
  100% { opacity: 0; transform: translateZ(-20px) rotateX(-90deg); }
}